// History Timeline Styling
.history-timeline {
  margin: 2rem auto;
  max-width: 1200px;
  padding: 0 1rem;

  .timeline-axis {
    position: relative;
    margin-left: clamp(8rem, 15vw, 11rem); // Responsive: adapts to screen width

    .timeline-line {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 3px;
      background: linear-gradient(to bottom,
        var(--global-theme-color) 0%,
        rgba(255, 68, 68, 0.7) 50%,
        var(--global-theme-color) 100%);
      border-radius: 1.5px;
      z-index: auto;
    }

    .timeline-events {
      padding-left: 2rem;
    }
  }

  .timeline-year-group {
    margin-bottom: 1.5rem;
    position: relative;

    .timeline-year-label {
      position: absolute;
      left: calc(-1 * clamp(8rem, 15vw, 11rem)); // Matches timeline-axis margin
      top: 0.1rem;
      width: clamp(6rem, 13vw, 9rem);
      text-align: right;
      font-weight: bold;
      font-size: 0.9rem;
      color: var(--global-theme-color);
      padding-right: 1rem;
      z-index: auto;
    }

    .timeline-events-container {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
    }
  }

  .timeline-event {
    position: relative;

    .timeline-event-marker {
      position: absolute;
      left: -2.15rem; // Centered on the line (3px line width / 2 + 10px marker width / 2)
      top: 0.1rem;
      width: 8px;
      height: 8px;
      background-color: var(--global-theme-color);
      border-radius: 50%;
      border: 2px solid var(--global-bg-color);
      box-shadow: 0 0 0 1px var(--global-theme-color);
      transition: all 0.3s ease;
      z-index: auto;

      &.spanning {
        border-radius: 2px;
        background: linear-gradient(45deg,
          var(--global-theme-color) 0%,
          rgba(255, 68, 68, 0.7) 50%,
          var(--global-theme-color) 100%);
      }
    }

    .timeline-event-content {
      color: var(--global-text-color);
      font-size: 0.9rem;
      line-height: 1.4;
      margin-bottom: 0.3rem;
    }

    &:hover .timeline-event-marker {
      transform: scale(1.5);
      box-shadow: 0 0 0 3px var(--global-theme-color), 0 0 10px rgba(255, 68, 68, 0.5);
      animation: pulse 1.5s ease-in-out infinite;
    }
  }

  @keyframes pulse {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.7;
    }
  }
}

