        padding-right: 0.5rem;
      }
    }

    .timeline-event {
      .timeline-event-marker {
        left: -1.75rem;
      }

      .timeline-event-content {
        font-size: 0.85rem;
      }
    }
  }
}

@media (max-width: 480px) {
  .history-timeline {
    .timeline-axis {
      margin-left: 5rem; // Extra small screens

      .timeline-events {
        padding-left: 1rem;
      }
    }

    .timeline-year-group {
      .timeline-year-label {
        left: -5rem;
        width: 4rem;
        font-size: 0.7rem;
      }
    }

    .timeline-event {
      .timeline-event-content {
        font-size: 0.8rem;
      }
    }
  }
}

// News section styling
.news {
  table {
    td, th {
      color: var(--global-text-color) !important;
    }
    
    th {
      width: 150px;
      min-width: 150px;
      white-space: nowrap;
    }
    
    .news-title {
      color: var(--global-theme-color) !important;
      
      &:hover {
        color: var(--global-hover-color) !important;
      }
    }
  }
}

a,
table.table a {
  color: var(--global-theme-color);

  &:hover {
    color: var(--global-theme-color);
    text-decoration: underline;
  }

  &:hover:after :not(.nav-item.dropdown) {
    width: 100%;
  }
}

figure,
img {
  max-width: 90vw;
}

blockquote {
  background: var(--global-bg-color);
  border-left: 2px solid var(--global-theme-color);
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  font-size: 1rem;
}

// Math

.equation {
  margin-bottom: 1rem;
  text-align: center;
}

// Caption

.caption {
  font-size: 0.875rem;
  margin-top: 0.75rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

// Card

.card {
  background-color: var(--global-card-bg-color);

  img {
    width: 100%;
  }

  .card-title {
    color: var(--global-text-color);
  }

  .card-item {
    width: auto;
    margin-bottom: 10px;

    .row {
      display: flex;
      align-items: center;
    }
  }
}

// Citation

.citation,
.citation-number {
  color: var(--global-theme-color);
}

// Profile

.profile {
  width: 100%;

  .address {
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: monospace;

    p {
      display: inline-block;
      margin: 0;
    }
  }
}

.profile.float-right {
  margin-left: 1rem;
}

.profile.float-left {
  margin-right: 1rem;
}

// Fixed profile for about/home page only
.post .profile.float-left {
  @media (min-width: 1200px) {
    position: fixed !important;
    left: calc((100vw - 800px) / 2 - 350px);
    top: 120px;
    width: 280px;
    margin-right: 0;
    z-index: 900;
  }

  img {
    @media (min-width: 1200px) {
      width: 100%;
      height: auto;
      filter: drop-shadow(0 0 15px rgba(255, 68, 68, 0.4));
      transition: filter 0.3s ease;

      &:hover {
        filter: drop-shadow(0 0 25px rgba(255, 68, 68, 0.6));
      }
    }
  }
}

// Mobile profile - centered and properly sized
@media (max-width: 767px) {
  .profile.float-left,
  .profile.float-right {
    float: none !important;
    width: 50% !important;
    max-width: 200px;
    margin: 0 auto 1.5rem !important;
    display: block;
  }

  .post .profile.float-left {
    float: none !important;
    width: 50% !important;
    max-width: 200px;
    margin: 0 auto 1.5rem !important;
  }
}

@media (min-width: 576px) {
  .profile {
    width: 30%;

    .address {
      p {
        display: block;
      }
    }
  }
}

.post-description {
  margin-bottom: 2rem;
  font-size: 0.875rem;

  a {
    color: inherit;

    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}


// Navbar customization

.navbar {
  box-shadow: none;
  border-bottom: 1px solid var(--global-divider-color);
  background-color: var(--global-bg-color);
  opacity: 0.95;
  z-index: 1030;
}

.navbar .dropdown-menu {
  background-color: var(--global-bg-color);
  border: 1px solid var(--global-divider-color);

  a:not(.active) {
    color: var(--global-text-color);
  }

  a:hover {
    color: var(--global-hover-color);
  }

  .dropdown-divider {
    border-top: 1px solid var(--global-divider-color) !important;
  }
}

.dropdown-item {
  color: var(--global-text-color);

  &:hover {
    color: var(--global-hover-color);
    background-color: var(--global-bg-color);
  }
}

.navbar.navbar-light {
  a {
    &:hover {
      text-decoration: none;
    }
  }

  .navbar-brand {
    color: var(--global-text-color);
  }

  .navbar-nav .nav-item .nav-link {
    color: var(--global-text-color);

    &:hover {
      color: var(--global-hover-color);
    }
  }

  .navbar-nav .nav-item.active>.nav-link {
    background-color: inherit;
    font-weight: bolder;
    color: var(--global-theme-color);

    &:hover {
      color: var(--global-hover-color);
    }
  }

  .navbar-brand.social {
    padding-bottom: 0;
    padding-top: 0;
    font-size: 1.7rem;

    a {
      i::before {
        color: var(--global-text-color);
        transition-property: all 0.2s ease-in-out;
      }

      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }
}

.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: var(--global-text-color);
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }

  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }

  .middle-bar {
    opacity: 0;
  }

  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }

  .middle-bar {
    opacity: 1;
  }

  .bottom-bar {
    transform: rotate(0);
  }
}

#light-toggle {
  padding: 0;
  border: 0;
  background-color: inherit;
  color: var(--global-text-color);

  &:hover {
    color: var(--global-hover-color);
  }
}

// Social (bottom)

.social {
  text-align: center;

  .contact-icons {
    font-size: 2.5rem;

    a {
      i::before {
        color: var(--global-text-color);
        opacity: 0.6;
        transition-property: all 0.2s ease-in-out;
      }

      &:hover {
        i::before {
          color: var(--global-theme-color);
          opacity: 1;
        }
      }
    }
  }

  .contact-note {
    font-size: 0.8rem;
  }
}


// Footer
footer.fixed-bottom {
  background-color: var(--global-footer-bg-color);
  font-size: 0.75rem;

  .container {
    color: var(--global-footer-text-color);
    padding-top: 9px;
    padding-bottom: 8px;
  }

  a {
    color: var(--global-footer-link-color);

    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}

footer.sticky-bottom {
